// Tile size variables - set dynamically
:root {
  --tile-width: 125px;
  --tile-height: 125px;
}

// for top layer of tiles on board
.overlay {
  position: absolute !important;
  z-index: 2 !important;
}

// new tile 
.new {
  transform: scale(0);
  animation: new linear forwards;
  animation-duration: 0.15s;
  animation-delay: 0.35s;
}

@keyframes new {
  from {
    transform: scale(0);
  }

  to {
    transform: scale(1);
  }
}

// Tile Combined
.combined {
  animation: combine-pop linear forwards;
  animation-delay: 0.2s;
  animation-duration: 0.15s;
}

@keyframes combine-pop {
  0% {
    transform: scale(0.6);
  }

  50% {
    transform: scale(1.3);
  }

  100% {
    transform: scale(1);
  }
}


// SLIDE RIGHT
.hor-r-1 {
  animation: slideRight1 0.2s linear forwards;
}

.hor-r-2 {
  animation: slideRight2 0.2s linear forwards;
}

.hor-r-3 {
  animation: slideRight3 0.2s linear forwards;
}

@keyframes slideRight1 {
  from {
    transform: translateX(calc(var(--tile-width) * -1));

  }

  to {
    transform: translateX(0);
  }
}

@keyframes slideRight2 {
  from {
    transform: translateX(calc(var(--tile-width) * -2));
  }

  to {
    transform: translateX(0);
  }
}

@keyframes slideRight3 {
